// Keyframes ===============================

@keyframes shake { // bell icon
  20% {
    transform: scale(1.1);
  }
  50% {
    transform: rotate(-10deg) scale(1.1);
  }
  80% {
    transform: rotate(5deg) scale(1.1);
  }
  100% {
    transform: rotate(0) scale(1);
  }
}

@keyframes slideDown { // alert slideDown
  0% {
    height: 0px;
    padding-top: 0;
    padding-bottom: 0;
  }
  100% {
    height: 44px;
    padding-top: 15px;
    padding-bottom: 15px;
    visibility: visible;
    margin: 0 0 20px;
  }
}

@keyframes slideUp { //alert slideUp
  0% {
    height: 44px;
    padding-top: 15px;
    padding-bottom: 15px;
    visibility: visible;
    margin: 0 0 20px;
  }
  100% {
    height: 0px;
    padding-top: 0;
    padding-bottom: 0;
    display: none;
  }
}
