// Main Content ----------------->

main {
  @include grid(main);
  color: #535353;
  width: 100%;
}

h2, h3 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 0;
  display: inline-block;
}

h2 {
  font-size: 1.1rem;
}

h3 {
  text-transform: uppercase;
  font-size: .9rem;
}

// All sections
.card {
  background: #fff;
  &:not(.dashboard__top) {
    padding: 20px;
    @include mq(768px) {
      padding: 30px;
    }
  }
}

.dashboard {
  &__top {
    @include grid(dash-top);
    display: grid;
    grid-template-columns: minmax(min-content,125px) 1fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas:
      "dash-title dash-search"
      "dash-traffic dash-traffic";
    & div {
      display: flex;
      align-items: center;
      &.dash-title {
        padding-left: 20px;
        @include grid(dash-title);
      }
      &.search {
        padding-right: 20px;
        justify-content: flex-end;
        background: $bg;
        @include grid(dash-search);
      }
    }
    &-search {
      border: 1px solid #e3e3e3;
      outline: none;
      font-size: .9rem;
      background-image: url(../icons/search.png);
      background-position: 95% 50%;
      background-repeat: no-repeat;
      padding: 0 10px;
      height: 35px;
      width: 50%;
      min-width: 100px;
      max-width: 380px;
      border-radius: 3px;
      transition: width .2s ease-out,
                  border .2s ease-out;
      &::placeholder {
        font-style: italic;
        opacity: 0;
      }
      &:focus {
        width: 70%;
        background-image: none;
        border: 2px solid $accent;
      }
    }
  }
  &__traffic { //===================================> Traffic Section
    @include grid(dash-traffic);
    & h3 {
      padding-top: 5px;
    }
    & .timeframes {
      padding: 0;
      margin: 0;
      float: right;
    }
    & input { // Traffic timeframe items ---------->
      display: none;
      &:checked + label {
        background: $accent;
        color: #fff;
      }
    }
    & label {
      display: inline-block;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      font-size: .85rem;
      margin: 0 5px;
      padding: 5px 10px;
      border-radius: 12px;
      transition: background .2s cubic-bezier(.87,-0.02,.35,1.18);
      &:hover {
        @extend %hover;
        color: $accent;
      }
    }
  }
  &__daily { //===================================> Daily Traffic Section
    @include grid(dash-daily);
  }
  &__mobile { //===================================> Mobile Section
    @include grid(dash-mobile);
  }
  &__social { //===================================> Social Section
    @include grid(dash-social);
    & .wrapper {
      padding: 10px;
      border: 1px solid darken($bg, 8%);
      border-radius: 4px;
      margin: 0 5px;
      width: calc(100% /3 - 10px);
      max-width: 300px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      @include mq(768px) {
        padding: 20px;
      }
      @include mq(1024px) {
        display: grid;
        grid-template-columns: 1fr 1.5fr;
        grid-template-rows: 1fr 4fr;
        grid-template-areas:
          "icon heading"
          "icon number";
        margin: 0 20px;
        width: calc(100% /3 - 40px);
      }
    }
    &-items {
      width: 100%;
      display: flex;
      justify-content: center;
      padding: 20px 0;
    }
    & .icon {
      background: darken($accent,12%);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 55px;
      height: 55px;
      margin-bottom: 10px;
      @include grid(icon);
      @include mq(768px) {
        width: 65px;
        height: 65px;
      }
      @include mq(1024px) {
        width: 80px;
        height: 80px;
      }
      &.twitter svg {
        transform: translateY(3px);
      }
      &.facebook svg {
        transform: scale(1.1) translateY(-1px);
      }
      &.googleplus svg {
        transform: translateY(2px) scale(.9);
      }
    }
    & svg {
      fill: #fff;
      width: 30px;
      height: 30px;
      transform-origin: 50% 50%;
      transform-box: fill-box;
      @include mq(768px) {
        width: 35px;
        height: 35px;
      }
      @include mq(1024px) {
        width: 40px;
        height: 40px;
      }
    }
    & h4 {
      margin: 0;
      color: $accent;
      display: none;
      font-size: .95rem;
      letter-spacing: .2px;
      @include grid(heading);
      @include mq(768px) {
        display: inline-block;
      }
      @include mq(1024px) {
        font-size: 1rem;
        align-self: flex-end;
      }
    }
    & span {
      margin: 0 0 auto;
      font-size: 1.2rem;
      font-family: 'Open Sans', sans-serif;
      color: darken($bg, 30%);
      @include grid(number);
      @include mq(768px) {
        font-size: 1.9rem;
      }
      @include mq(1024px) {
        font-size: 3rem;
      }
    }
  }
  &__members { //===================================> Members Section
    @include grid(dash-members);
  }
  &__recent { //====================================> Recent Activity Section
    @include grid(dash-recent);
  }
  &__msg { //=======================================> Message Section
    @include grid(dash-msg);
  }
  &__settings { //==================================> Settings Section
    @include grid(dash-settings);
  }
}
